<template>
  <div class="onewrap">
    <div class="movie">
      <video  id="videoz" ref="video" preload="metadate">
        <source src="./static/video/zhuchi.mp4"  type="video/mp4">
      </video>
    </div>
    <div class="onewin">
      <img  class="host " src="../assets/host.png">
      <v-touch @tap="showvideo" class="moveShow"></v-touch>
      <img class="font" src="../assets/font.png">   
    </div>
   </div>
</template>

<script>
export default {
  data () {
    return {
      // vodeo:false
    }
  },
  mounted:function(){

  },
  methods:{
    showvideo(){
       this.$refs.video.play();      
    }
  }
}
</script>
<style scoped lang="scss" scoped>
  .movie{
    position:absolute;
    display: none;
  }
  .moveShow{
    background:url("../assets/btn.png") no-repeat center /100%;
    opacity:0;
    width:2.5rem;
    height:2.5rem;
    z-index:9999;
    position:absolute;
    top:6.3rem;
    left:4rem;
    animation: show 2s ease 2s forwards;
    //animation-fill-mode:;
  }
   .onewin{
    position:relative;
    width:10rem;
    height:16.25rem;
    overflow: hidden;
    opacity:0;
    background:url("../assets/onewinz.jpg") no-repeat center /100% 100% ;
     animation:showBg 5s ease 0.1s forwards;
      .host{
        position:relative;
        top:3.8rem;
        width:6.55rem;
        left:1.528rem;
        height:auto;
        animation:hostshow 1.8s ease 0.2s forwards;
      }
      .font{
        width:6.9875rem;
        height:auto;
        position:relative;
        z-index:999;
        top:-5.4rem;
        animation:wobble 1.5s ease 0.5s ;
      }

   }

   @keyframes show{
     0%{
       opacity: 0
     }
     100%{
       opacity:0.8;
     }
   }
   @keyframes showBg{
     0%{
       opacity: 0
     }
     100%{
       opacity:1;
     }
   }
   @keyframes hostshow{
     0%{
        top:3.8rem;
        left:3.5rem;
     }
     100%{
        top:3.8rem;
        left:1.4rem;
     }
   }
</style>



